import React, { useState, useEffect } from "react";
import { useGetLocale } from "providers";

import { Button, chatPipeFunctions, Table } from "components";
import { getIntlValue } from "providers";
import { chatSocket } from "utils";
import { useCoins, useModalLocation } from "hooks";
import { throttle } from "utils/lodash";

import { useUser } from "providers/User";
// import { chatSocketsList } from "components/Chat/chatSocketWatcher";
// import { StatisticMyBetsTypes } from "components/Chat/chatWatcherTypes";

import { createBetsTableData } from "./createBetsTableData";
import css from "./style.module.scss";

export interface YourBetsElement {
  id: string;
  payout: string;
  hash: string;
}

export interface BetType {
  game: string;
  bet_amt: string;
  payout: string;
  profit: string;
  id: string;
  user_name?: string;
  coin?: string;
}

interface Props {
  open: boolean;
}

interface PathGame {
  "/hash-dice": "Dice";
  "/crash": "Crash";
  "/plinko": "Plinko";
  "/limbo": "Limbo";
  "/video-poker": "Video Poker";
  "/roulette": "Roulette";
  "/blackjack": "Blackjack";
  "/towers": "Towers";
  [key: string]: string;
}

export const pathGame: PathGame = {
  "/hash-dice": "Dice",
  "/crash": "Crash",
  "/plinko": "Plinko",
  "/limbo": "Limbo",
  "/video-poker": "Video Poker",
  "/roulette": "Roulette",
  "/blackjack": "Blackjack",
  "/towers": "Towers",
};

const wait = throttle((f: () => void) => {
  f();
}, 500);

interface SocketListenerValues {
  betsList?: BetType[];
  setBetsList?: (v: BetType[]) => void;
}
const socketListenerValues: SocketListenerValues = {};

const updateBetList = (values: BetType[]) => {
  const { betsList, setBetsList } = socketListenerValues;
  if (betsList && setBetsList) {
    const bets = [...values, ...betsList];

    if (bets.length > 10) {
      bets.length = 10;
    }

    setBetsList(bets);
    values.length = 0;
  }
};

const YourBets = ({ open }: Props) => {
  const intl = useGetLocale();

  const [betsList, setBetsList] = useState<BetType[]>([]);
  const { coinsPrices, showInUSD } = useCoins(["coinsPrices", "showInUSD"]);

  socketListenerValues.betsList = betsList;
  socketListenerValues.setBetsList = setBetsList;

  const { pushHistoryTo, location, close } = useModalLocation("bet-data");
  const { user } = useUser();

  const getTableHeaderName = getIntlValue(
    intl,
    "gameTemplate.statistics.yourBetsTable.tHead.",
  );

  const handleOpenData = (id: string, user_name: string) => {
    pushHistoryTo({
      searchParams: {
        id,
        userName: user_name,
      },
    });
  };

  const handleHistoryOpen = () => {
    pushHistoryTo({ search: { name: "page", value: "game-history" } });
  };

  useEffect(() => {
    if (open) {
      const interim: BetType[] = [];

      const listener = (data: any) => {
        // data: StatisticMyBetsTypes

        switch (data.action) {
          case "my_bets":
            if (data.bets) {
              if (data.bets?.length) {
                setBetsList(data.bets);
              } else if (typeof data.bets === "object") {
                setBetsList(Object.values(data.bets));
              }
            }
            break;

          case "new_bet_received":
            const bet = {
              bet_amt: data.bet_amt,
              coin: data.coin,
              game: data.game,
              id: data.id,
              payout: data.payout,
              profile_picture_url: data.profile_picture_url,
              profit: data.profit,
              user_name: data.user_name,
            };

            if (bet.user_name === user?.user_name) {
              interim.unshift(bet);

              wait(() => {
                updateBetList(interim);
              });
            }

            break;

          case "set_user_parameters":
            chatSocket.send({ type: "get_my_bets" });
            break;

          default:
            break;
        }
      };

      chatSocket.send({ type: "get_my_bets" });
      chatPipeFunctions.statisticMyBetsListener = listener;
    } else {
      chatPipeFunctions.statisticMyBetsListener = () => null;
    }

    return () => {
      chatPipeFunctions.statisticMyBetsListener = () => null;
    };
  }, [open, user?.user_name]);

  if (!open) {
    return null;
  }

  const tableData = createBetsTableData(
    betsList,
    showInUSD,
    coinsPrices,
    user?.user_name || "",
    handleOpenData,
  );
  const game =
    location.state && location.state.background
      ? location.state.background.pathname
      : location.pathname;

  return (
    <div className={css.wrapper}>
      <div className={css.tableWrapper}>
        <Table
          head={{
            values: [
              getTableHeaderName("game"),
              getTableHeaderName("payout"),
              getTableHeaderName("betAmt"),
              getTableHeaderName("profit"),
            ],
          }}
          body={{
            rows: tableData,
            classes: {
              td: css.td,
            },
          }}
        />

        <div className={css.allButtonWrapper}>
          <Button onClick={handleHistoryOpen}>
            {intl.formatMessage(
              { id: "gameTemplate.statistics.yourBetsTable.allHistoryButton" },
              {
                game: pathGame[game],
              },
            )}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default YourBets;
