import React from "react";
import clsx from "clsx";

import { letterFormat, replaceNumbers } from "utils";
import { GetCoinImg, Typography } from "components";
import { CoinPriceList } from "hooks";

import css from "./style.module.scss";
import { BetType } from "./YourBets";

export const createBetsTableData = (
  betsList: BetType[],
  showUSD: boolean,
  coinPrice: CoinPriceList,
  userName: string,
  callBack: (id: string, user_name: string) => void,
) =>
  betsList.slice(0, 10).map(({ game, payout, bet_amt, profit, coin, id }) => {
    if (game === "VIDEOPOKER") {
      game = "VIDEO POKER";
    }
    bet_amt =
      showUSD && coin && coin !== "GEM"
        ? (Number(bet_amt) * Number(coinPrice[coin]?.price))
            .toFixed(8)
            .toString()
        : bet_amt;
    profit =
      showUSD && coin && coin !== "GEM"
        ? (Number(profit) * Number(coinPrice[coin]?.price))
            .toFixed(8)
            .toString()
        : profit;

    const values = [
      <Typography className={css.tableContent}>
        {letterFormat(game, true)}
      </Typography>,

      <Typography className={css.tableContent}>{payout}x</Typography>,

      <Typography className={clsx(css.coinWrapper, css.tableContent)}>
        <GetCoinImg coin={coin || "BTC"} className={css.coinIcon} />
        {showUSD && coin !== "GEM" ? "$" : null}
        {replaceNumbers(bet_amt)}
      </Typography>,

      <Typography
        className={clsx(
          Number(profit) < 0 ? css.profitLose : css.profit,
          css.tableContent,
        )}>
        {replaceNumbers(profit)}
      </Typography>,
    ];

    return {
      values,
      key: id,
      callBack: () => callBack(id, userName),
    };
  });
