import React from "react";
import { ReactComponent as NavigateBeforeIcon } from "assets/icons/navigate-before.svg";

import css from "./style.module.scss";

interface Props {
  disabledBefore: boolean;
  disabledAfter: boolean;
  page: number;
  setNextPage: Function;
  setPrevPage: Function;
  className?: string;
}

const rotateStyle = { transform: "rotate(180deg)" };

const SimplePagination = ({
  disabledBefore,
  disabledAfter,
  setNextPage,
  setPrevPage,
  page,
  className,
}: Props) => {
  return (
    <div className={`${css.content} ${className ? className : ""}`}>
      <li
        className={`${css.number} ${disabledBefore ? css.disable : ""}`}
        onClick={() => {
          !disabledBefore && setPrevPage();
        }}>
        <NavigateBeforeIcon className={css.icon} />
      </li>
      <span>{page}</span>
      <li
        className={`${css.number} ${disabledAfter ? css.disable : ""}`}
        style={rotateStyle}
        onClick={() => {
          !disabledAfter && setNextPage();
        }}>
        {<NavigateBeforeIcon className={css.icon} />}
      </li>
    </div>
  );
};

export default SimplePagination;
