import React from "react";
import { useGetLocale } from "providers";

import { getIntlValue, useWindowSize } from "providers";
import { GetCoinImg, Table, Typography } from "components";

import { mobileScreen } from "sass/styleVariables";

import { Bet } from "../..";
import css from "./style.module.scss";

import { Scrollbar } from "components/Scrollbar";

interface Props {
  data: Bet[] | undefined;
}

const scrollStyle = (isSM: boolean) => {
  return { height: isSM ? 500 : 660, width: "100%" };
};

const GameHistoryTable = ({ data }: Props) => {
  const intl = useGetLocale();
  const { width } = useWindowSize();
  const isSm = width <= mobileScreen;

  const formatMessage = getIntlValue(intl, "gameHistory.rows.");

  return (
    <Scrollbar style={scrollStyle(isSm)}>
      <Table
        root={{
          className: css.table,
        }}
        head={{
          values: [
            formatMessage("id"),
            formatMessage("amount"),
            formatMessage("profit"),
            formatMessage("payout"),
          ],
          classes: {
            thead: css.tableHead,
            th: css.tableCell,
          },
        }}
        body={{
          rows: data?.map(({ bet_amt, coin, id, payout, profit, win }) => {
            return {
              values: [
                <div className={css.tableCellWrapper}>
                  <div
                    className={`${css.profit} ${
                      win ? css.profitWin : css.profitLose
                    }`}
                  />
                  {id}
                </div>,

                <div className={css.tableCellWrapper}>
                  <GetCoinImg coin={coin} className={css.coinIcon} />
                  <Typography>{bet_amt}</Typography>
                </div>,

                <div className={css.tableCellWrapper}>
                  <GetCoinImg coin={coin} className={css.coinIcon} />
                  {profit}
                </div>,

                <div className={css.tableCellWrapper}>
                  <div
                    className={`${css.profitBox} ${
                      win ? css.profitWin : css.profitLose
                    }`}>
                    {Number(payout).toFixed(2)}x
                  </div>
                </div>,
              ],
              key: id + bet_amt,
            };
          }),
          classes: {
            tr: css.content,
            td: css.tableCell,
            tbody: css.tableBody,
          },
        }}
      />
    </Scrollbar>
  );
};

export default GameHistoryTable;
