import React, {
  FunctionComponent,
  useCallback,
  useEffect,
  useState,
} from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { useFetch, useLocalStorage, useModalLocation } from "hooks";
import { Icon } from "components";

import { pathGame as basePathGame } from "components/Statistics/Tabs/YourBets/YourBets";
import { ModalsProps } from "config/routes";
import { Dialog } from "containers/PopUps";

import Table from "./components/Table/index";
import Pagination from "./components/Pagination/index";
import css from "./style.module.scss";
import { useWindowSize } from "providers";

export interface Bet {
  bet_amt: string;
  coin: string;
  id: number;
  net_profit_inclusive_usd: string;
  payout: string;
  profit: string;
  win: number;
}

interface Fetch {
  bets: Bet[];
}

const pathGame: any = {
  ...basePathGame,
  "/video-poker": "Videopoker",
  "/mines": "MINES",
  "/keno": "KENO",
  "/diamonds": "DIAMONDS",
};

const GameHistory: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const intl = useGetLocale();
  const { close, location } = useModalLocation("game-history");
  const [token] = useLocalStorage("token");

  const [page, setPage] = useState(1);
  const [bets, setBets] = useState<Bet[]>();

  const { isMobile } = useWindowSize();

  const [fetch, { data, loading }] = useFetch<Fetch>("statistics/bets");

  useEffect(() => {
    if (token && isOpen) {
      let game = pathGame[location.pathname];
      if (!game && location.pathname.includes("slot")) {
        game = "SLOTS";
      }
      fetch({
        variables: {
          token,
          page,
          game,
        },
      });
    } else if (!isOpen && page !== 1) {
      setPage(1);
    }
  }, [token, page, isOpen]);

  useEffect(() => {
    if (data) {
      if (data.bets.length > 0) {
        setBets(data.bets);
      } else if (page > 0) {
        setPage(page - 1);
      }
    }
  }, [data]);

  const setNextPage = useCallback(() => {
    setPage(page + 1);
  }, [page, setPage]);
  const setPrevPage = useCallback(() => {
    setPage(page - 1);
  }, [page, setPage]);

  const handleClose = () => {
    close();
  };
  const game =
    location.state && location.state.background
      ? location.state.background.pathname
      : location.pathname;

  return (
    <Dialog
      isOpen={isOpen}
      paper={{
        className: css.paper,
      }}
      fullScreen={isMobile}
      onClose={handleClose}>
      <div className={css.title}>
        <span>
          {intl.formatMessage(
            { id: "gameTemplate.statistics.yourBetsTable.allHistoryButton" },
            {
              game: pathGame[game],
            },
          )}
        </span>
        <Icon
          variant="close"
          className={css.closeIcon}
          onClick={handleClose}
          fontSize={12}
        />
      </div>

      <div className={css.box}>
        {bets ? (
          <Table data={bets} />
        ) : (
          <h1 className={clsx(css.title, css.subTitle)}>
            {intl.formatMessage({
              id: "wallet.emptyTable",
            })}
          </h1>
        )}
      </div>
      <Pagination
        disabledBefore={loading || page <= 1 || !bets}
        disabledAfter={loading || !bets || bets.length < 15}
        page={page}
        setNextPage={setNextPage}
        setPrevPage={setPrevPage}
        className={css.pagination}
      />
    </Dialog>
  );
};

export default GameHistory;
